<!-- @format -->

<template>
  <div class="companies">
    <h1></h1>
    <el-row class="operationGroup" type="flex" justify="between">
      <el-col class="operationGroup-left" :span="4">
        <el-button
          type="danger"
          @click="deleteCompanies"
          v-if="!!deleteCompaniesId.length"
          ></el-button
        >
      </el-col>
      <el-col class="operationGroup-right" :xs="18" :sm="8">
        <el-input v-model="newCompanyName" placeholder="">
          <el-button
            slot="append"
            @click="sendNewCompany"
            :disabled="!newCompanyName"
          >
            <i class="el-icon-plus"></i>
          </el-button>
        </el-input>
      </el-col>
    </el-row>
    <el-table
      :data="companies"
      class="companies-table"
      @selection-change="updateDeleteList"
    >
      <el-table-column type="selection" width="40"> </el-table-column>
      <el-table-column prop="name" label="">
        <template slot-scope="scope">
          <el-input
            v-model="scope.row.name"
            @focus="beforeEdit(scope.row.id, scope.row.name)"
            @blur="afterEdit(scope.row.id, scope.row.name)"
          >
          </el-input>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import ToPathMixin from '@/mixins/ToPath'
export default {
  mixins: [ToPathMixin],
  data() {
    return {
      oldCompany: {
        id: '',
        name: ''
      },
      deleteCompaniesId: [],
      newCompanyName: ''
    }
  },
  computed: {
    companies() {
      return this.$store.getters.companies
    }
  },
  methods: {
    async sendNewCompany() {
      try {
        await this.$store.dispatch('createCompany', {
          name: this.newCompanyName
        })
        this.$message({
          message: ` ${this.newCompanyName}`,
          type: 'success',
          center: true,
          duration: 1800
        })
        this.newCompanyName = ''
      } catch (e) {
        this.$message.error(` ${e.message}`)
      }
    },
    deleteCompanies() {
      if (this.deleteCompaniesId.length === 0) return
      this.$confirm('', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          this.$store
            .dispatch('deleteCompanies', this.deleteCompaniesId)
            .then(() => {
              this.$message({
                message: ``,
                type: 'success',
                center: true,
                duration: 1800
              })
            })
            .catch(e => {
              this.$message.error(` ${e.message}`)
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    updateDeleteList(value) {
      this.deleteCompaniesId = value.map(company => company.id)
    },
    beforeEdit(id, name) {
      this.oldCompany = {
        id,
        name
      }
    },
    afterEdit(id, name) {
      if (name === this.oldCompany.name) return // not change
      this.$store
        .dispatch('updateCompany', {
          id,
          payload: { name }
        })
        .then(() => {
          this.$message({
            message: ` ${this.oldCompany.name}  ${name}`,
            type: 'success',
            center: true,
            duration: 1800
          })
        })
        .catch(e => {
          this.$message.error(` ${e.message}`)
        })
    }
  }
}
</script>
